.contact-modern {
  padding: 120px 0;
  background: white;
  position: relative;
  overflow: hidden;
}

/* Background Decoration */
.contact-bg-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
}

.decoration-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(80px);
  opacity: 0.1;
}

.circle-1 {
  width: 400px;
  height: 400px;
  background: var(--gradient);
  top: -100px;
  right: -100px;
}

.circle-2 {
  width: 500px;
  height: 500px;
  background: linear-gradient(135deg, #77b1d4, #517891);
  bottom: -150px;
  left: -150px;
}

.container {
  position: relative;
  z-index: 1;
}

/* Header */
.contact-header {
  text-align: center;
  margin-bottom: 4rem;
}

.header-badge-contact {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.5rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  color: var(--primary-color);
  font-weight: 700;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.badge-icon-contact {
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%,
  100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(1.1);
  }
}

.contact-title {
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.contact-subtitle {
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 650px;
  margin: 0 auto;
}

/* Main Content */
.contact-content {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  gap: 3rem;
}

/* Contact Info Side */
.contact-info-side {
  display: flex;
  flex-direction: column;
}

.info-card {
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 30px;
  padding: 3rem;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.info-card h3 {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.info-description {
  color: var(--text-secondary);
  margin-bottom: 2.5rem;
  line-height: 1.6;
}

/* Contact Methods */
.contact-methods {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.method-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.5rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 20px;
  transition: all 0.3s ease;
}

.method-item:hover {
  transform: translateX(10px);
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.15);
  border-color: var(--primary-color);
}

.method-icon {
  width: 55px;
  height: 55px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.method-info {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.method-title {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.method-value {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.method-value:hover {
  color: var(--primary-color);
}

/* Social Section */
.social-section {
  margin-top: auto;
  padding-top: 2rem;
  border-top: 2px solid rgba(87, 185, 255, 0.2);
}

.social-section h4 {
  font-size: 1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.social-links-contact {
  display: flex;
  gap: 1rem;
}

.social-btn-contact {
  width: 50px;
  height: 50px;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 1.3rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-btn-contact:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(87, 185, 255, 0.3);
}

/* Availability Badge */
.availability-badge {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1rem 1.5rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  margin-top: 2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.availability-dot {
  width: 12px;
  height: 12px;
  background: #10b981;
  border-radius: 50%;
  animation: pulse-availability 2s infinite;
}

@keyframes pulse-availability {
  0%,
  100% {
    box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.7);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(16, 185, 129, 0);
  }
}

/* Contact Form Side */
.contact-form-side {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 30px;
  padding: 3rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* Success Message */
.success-message {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  background: #d1fae5;
  border: 2px solid #10b981;
  border-radius: 16px;
  color: #059669;
  font-weight: 600;
  animation: slideDown 0.5s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.success-icon {
  font-size: 1.5rem;
}

/* Form Elements */
.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.form-group input,
.form-group textarea {
  padding: 1rem 1.5rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 16px;
  font-family: inherit;
  font-size: 1rem;
  color: var(--text-primary);
  transition: all 0.3s ease;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 4px rgba(87, 185, 255, 0.1);
}

.form-group textarea {
  resize: vertical;
  min-height: 150px;
}

/* Submit Button */
.submit-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  padding: 1.2rem 2rem;
  background: var(--gradient);
  color: white;
  border: none;
  border-radius: 16px;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.submit-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.submit-btn:hover::before {
  width: 300px;
  height: 300px;
}

.submit-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(87, 185, 255, 0.5);
}

.submit-btn:active {
  transform: translateY(0);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

.submit-btn svg,
.submit-btn span {
  position: relative;
  z-index: 1;
}

/* Spinner */
.spinner {
  width: 20px;
  height: 20px;
  border: 3px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive */
@media (max-width: 1024px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }

  .info-card {
    padding: 2.5rem;
  }
}

@media (max-width: 768px) {
  .contact-modern {
    padding: 80px 0;
  }

  .contact-title {
    font-size: 2rem;
  }

  .contact-subtitle {
    font-size: 1rem;
  }

  .info-card,
  .contact-form-side {
    padding: 2rem;
  }

  .form-row {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .method-item {
    padding: 1.2rem;
  }

  .method-icon {
    width: 50px;
    height: 50px;
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .info-card,
  .contact-form-side {
    padding: 1.5rem;
  }

  .contact-methods {
    gap: 1rem;
  }

  .submit-btn {
    width: 100%;
  }
}

/* Error Message */
.error-message {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  background: #fee2e2;
  border: 2px solid #ef4444;
  border-radius: 16px;
  color: #dc2626;
  font-weight: 600;
  animation: slideDown 0.5s ease;
  margin-bottom: 1.5rem;
}
