.about-fresh {
  padding: 120px 0;
  background: white;
  position: relative;
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s ease;
  overflow: hidden;
}

.about-fresh.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Background Elements */
.about-bg-elements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
}

.bg-circle {
  position: absolute;
  border-radius: 50%;
  filter: blur(100px);
  opacity: 0.08;
}

.circle-1 {
  width: 500px;
  height: 500px;
  background: var(--gradient);
  top: -100px;
  right: -100px;
}

.circle-2 {
  width: 600px;
  height: 600px;
  background: linear-gradient(135deg, #77b1d4, #517891);
  bottom: -200px;
  left: -200px;
}

.container {
  position: relative;
  z-index: 1;
}

/* Header */
.about-header-fresh {
  text-align: center;
  margin-bottom: 4rem;
}

.header-badge-about {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.5rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  color: var(--primary-color);
  font-weight: 700;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.badge-icon-about {
  animation: sparkle 2s ease-in-out infinite;
}

@keyframes sparkle {
  0%,
  100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(180deg) scale(1.1);
  }
}

.about-title-fresh {
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.about-subtitle-fresh {
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 650px;
  margin: 0 auto;
}

/* Grid Layout */
.about-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  grid-auto-rows: minmax(200px, auto);
}

/* Base Card Styles */
.about-card {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 24px;
  padding: 2.5rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.about-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.2);
  border-color: var(--primary-color);
}

/* Story Card */
.story-card {
  grid-column: span 4;

  background: var(--gradient);
  /* color: ; */
  border: none;
}

.card-icon-header {
  width: 60px;
  height: 60px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.header-icon {
  font-size: 2rem;
}

.story-card h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.story-content p {
  margin-bottom: 1.5rem;
  line-height: 1.8;
  opacity: 0.95;
}

.card-decoration {
  display: flex;
  gap: 0.5rem;
  margin-top: 2rem;
}

.decoration-dot {
  width: 8px;
  height: 8px;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 50%;
}

/* Stats Grid */
.stats-grid {
  grid-column: span 2;
  grid-row: span 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.stat-card-fresh {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 24px;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.stat-card-fresh:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.2);
  border-color: var(--primary-color);
}

.stat-icon {
  width: 50px;
  height: 50px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
  transition: all 0.3s ease;
}

.stat-card-fresh:hover .stat-icon {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: scale(1.1) rotate(5deg);
}

.stat-number {
  font-size: 2.5rem;
  font-weight: 800;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
  margin-bottom: 0.5rem;
}

.stat-label {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 600;
}

.stat-glow {
  position: absolute;
  bottom: -50%;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 100px;
  background: radial-gradient(circle, var(--primary-color), transparent);
  opacity: 0;
  filter: blur(30px);
  transition: all 0.3s ease;
}

.stat-card-fresh:hover .stat-glow {
  opacity: 0.2;
  bottom: -30%;
}

/* Expertise Grid */
.expertise-grid-about {
  grid-column: span 2;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.expertise-card-about {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 24px;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.expertise-card-about:hover {
  transform: translateY(-10px);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.2);
  border-color: var(--card-color);
}

.expertise-icon-about {
  width: 70px;
  height: 70px;
  border-radius: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  margin: 0 auto 1.5rem;
  transition: all 0.3s ease;
}

.expertise-card-about:hover .expertise-icon-about {
  transform: scale(1.1) rotate(5deg);
}

.expertise-card-about h4 {
  font-size: 1.1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.8rem;
}

.expertise-card-about p {
  font-size: 0.9rem;
  color: var(--text-secondary);
  line-height: 1.6;
}

/* Highlights Card */
.highlights-card {
  grid-column: span 2;
  grid-row: span 1;
  background: var(--light-color);
}

.highlights-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.highlights-icon {
  font-size: 2rem;
  color: var(--primary-color);
}

.highlights-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.highlights-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.highlight-item-fresh {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 16px;
  transition: all 0.3s ease;
}

.highlight-item-fresh:hover {
  transform: translateX(10px);
  border-color: var(--primary-color);
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.15);
}

.highlight-emoji {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.highlight-text {
  font-size: 0.95rem;
  color: var(--text-primary);
  font-weight: 500;
}

/* Values Card */
.values-card {
  grid-column: span 2;
  grid-row: span 1;
}

.values-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.values-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.value-item {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}

.value-number {
  width: 40px;
  height: 40px;
  background: var(--gradient);
  color: white;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;
  flex-shrink: 0;
}

.value-content h4 {
  font-size: 1rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.3rem;
}

.value-content p {
  font-size: 0.85rem;
  color: var(--text-secondary);
  line-height: 1.5;
}

/* Responsive */
@media (max-width: 1200px) {
  .about-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .story-card {
    grid-column: span 2;
  }

  .stats-grid {
    grid-column: span 2;
  }

  .expertise-grid-about {
    grid-column: span 2;
    grid-template-columns: repeat(2, 1fr);
  }

  .highlights-card,
  .values-card {
    grid-column: span 2;
  }
}

@media (max-width: 768px) {
  .about-fresh {
    padding: 80px 0;
  }

  .about-title-fresh {
    font-size: 2rem;
  }

  .about-subtitle-fresh {
    font-size: 1rem;
  }

  .about-grid {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
  }

  .story-card,
  .stats-grid,
  .expertise-grid-about,
  .highlights-card,
  .values-card {
    grid-column: span 1;
  }

  .stats-grid {
    grid-template-columns: 1fr;
  }

  .expertise-grid-about {
    grid-template-columns: 1fr;
  }

  .about-card {
    padding: 2rem;
  }

  .stat-number {
    font-size: 2rem;
  }
}
