.hero-new {
  min-height: 100vh;
  background: linear-gradient(135deg, #f0f8ff 0%, #ffffff 50%, #f0f8ff 100%);
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  padding: 100px 0 80px;
}

/* Background Elements */
.hero-bg-new {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 0;
}

.gradient-mesh {
  position: absolute;
  width: 100%;
  height: 100%;
  background: radial-gradient(
      circle at 20% 30%,
      rgba(144, 213, 255, 0.15),
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 70%,
      rgba(87, 185, 255, 0.15),
      transparent 50%
    ),
    radial-gradient(
      circle at 50% 50%,
      rgba(119, 177, 212, 0.1),
      transparent 50%
    );
  transition: transform 0.3s ease-out;
}

.floating-shapes {
  position: absolute;
  width: 100%;
  height: 100%;
}

.shape {
  position: absolute;
  opacity: 0.1;
  transition: transform 0.3s ease-out;
}

.shape-circle {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: var(--gradient);
  top: 10%;
  left: 10%;
  filter: blur(60px);
}

.shape-square {
  width: 200px;
  height: 200px;
  background: linear-gradient(135deg, #77b1d4, #517891);
  top: 60%;
  right: 15%;
  border-radius: 30px;
  filter: blur(50px);
  transform: rotate(45deg);
}

.shape-triangle {
  width: 0;
  height: 0;
  border-left: 150px solid transparent;
  border-right: 150px solid transparent;
  border-bottom: 260px solid rgba(87, 185, 255, 0.3);
  bottom: 10%;
  left: 50%;
  filter: blur(40px);
}

.grid-lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(
      rgba(87, 185, 255, 0.03) 1px,
      transparent 1px
    ),
    linear-gradient(90deg, rgba(87, 185, 255, 0.03) 1px, transparent 1px);
  background-size: 60px 60px;
}

/* Hero Container */
.hero-container-new {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 40px;
  position: relative;
  z-index: 1;
}

.hero-content-new {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

/* Left Side */
.hero-left {
  animation: fadeInLeft 1s ease forwards;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.2rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary-color);
  margin-bottom: 2rem;
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.1);
}

.badge-icon {
  animation: rotate 3s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hero-heading {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 1.5rem;
}

.greeting {
  font-size: 1.5rem;
  color: var(--text-secondary);
  font-weight: 500;
}

.name-large {
  font-size: 4.5rem;
  font-weight: 800;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1.1;
  letter-spacing: -2px;
}

.role-line {
  font-size: 2.5rem;
  font-weight: 600;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.role-text {
  color: var(--dark-color);
}

.cursor-line {
  animation: blink 1s infinite;
  color: var(--primary-color);
}

@keyframes blink {
  0%,
  50% {
    opacity: 1;
  }
  51%,
  100% {
    opacity: 0;
  }
}

.hero-desc {
  font-size: 1.2rem;
  color: var(--text-secondary);
  line-height: 1.8;
  margin-bottom: 2.5rem;
  max-width: 600px;
}

/* Action Buttons */
.hero-actions {
  display: flex;
  gap: 1rem;
  margin-bottom: 3rem;
  flex-wrap: wrap;
}

.btn-hero-primary,
.btn-hero-secondary {
  display: inline-flex;
  align-items: center;
  gap: 0.8rem;
  padding: 1.2rem 2rem;
  text-decoration: none;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.btn-hero-primary {
  background: var(--gradient);
  color: white;
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.3);
}

.btn-hero-primary::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.btn-hero-primary:hover::before {
  width: 400px;
  height: 400px;
}

.btn-hero-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(87, 185, 255, 0.5);
}

.btn-hero-secondary {
  background: white;
  color: var(--primary-color);
  border: 2px solid var(--secondary-color);
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.1);
}

.btn-hero-secondary:hover {
  background: var(--light-color);
  border-color: var(--primary-color);
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.2);
}

.btn-icon {
  position: relative;
  z-index: 1;
  transition: transform 0.3s ease;
}

.btn-hero-primary:hover .btn-icon,
.btn-hero-secondary:hover .btn-icon {
  transform: translateX(5px);
}

/* Stats */
.hero-stats {
  display: flex;
  gap: 2rem;
}

.stat-box {
  padding: 1rem 1.5rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.1);
}

.stat-box h3 {
  font-size: 2rem;
  font-weight: 700;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 0.25rem;
}

.stat-box p {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 600;
}

/* Right Side - Visual */
.hero-right {
  animation: fadeInRight 1s ease forwards;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero-visual {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Profile Card */
.profile-card {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 30px;
  padding: 2.5rem;
  box-shadow: 0 20px 60px rgba(87, 185, 255, 0.15);
  position: relative;
  z-index: 2;
  max-width: 400px;
}

.card-glow {
  position: absolute;
  inset: -2px;
  background: var(--gradient);
  border-radius: 30px;
  opacity: 0;
  filter: blur(20px);
  transition: opacity 0.3s ease;
  z-index: -1;
}

.profile-card:hover .card-glow {
  opacity: 0.5;
}

.profile-image {
  position: relative;
  margin-bottom: 2rem;
}

.image-placeholder {
  width: 150px;
  height: 150px;
  background: var(--gradient);
  border-radius: 50%;
  margin: 0 auto 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 5rem;
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.3);
  position: relative;
  overflow: hidden;
}

.image-placeholder::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    45deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  animation: shine 3s infinite;
}

@keyframes shine {
  0% {
    transform: translateX(-100%) translateY(-100%) rotate(45deg);
  }
  100% {
    transform: translateX(100%) translateY(100%) rotate(45deg);
  }
}

.status-indicator {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  width: fit-content;
  margin: 0 auto;
}

.status-dot {
  width: 10px;
  height: 10px;
  background: #10b981;
  border-radius: 50%;
  animation: pulse-dot 2s infinite;
}

@keyframes pulse-dot {
  0%,
  100% {
    box-shadow: 0 0 0 0 rgba(16, 185, 129, 0.7);
  }
  50% {
    box-shadow: 0 0 0 10px rgba(16, 185, 129, 0);
  }
}

.status-text {
  font-size: 0.9rem;
  font-weight: 600;
  color: #10b981;
}

/* Quick Info */
.quick-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.info-label {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-weight: 600;
}

.info-value {
  font-size: 0.95rem;
  color: var(--text-primary);
  font-weight: 500;
}

/* Social Links in Card */
.social-links-card {
  display: flex;
  gap: 0.8rem;
  justify-content: center;
  padding-top: 1.5rem;
  border-top: 2px solid var(--light-color);
}

.social-btn {
  width: 45px;
  height: 45px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 1.2rem;
  transition: all 0.3s ease;
  text-decoration: none;
}

.social-btn:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: translateY(-5px) rotate(5deg);
  box-shadow: 0 10px 25px rgba(87, 185, 255, 0.3);
}

/* Floating Badges */
.floating-badge {
  position: absolute;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.5rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(87, 185, 255, 0.15);
  animation: float 3s ease-in-out infinite;
}

.badge-1 {
  top: 20%;
  right: -10%;
  animation-delay: 0s;
}

.badge-2 {
  bottom: 15%;
  left: -10%;
  animation-delay: 1.5s;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
}

.badge-icon-float {
  font-size: 2rem;
}

.badge-text {
  display: flex;
  flex-direction: column;
}

.badge-title {
  font-size: 0.95rem;
  font-weight: 700;
  color: var(--text-primary);
}

.badge-subtitle {
  font-size: 0.8rem;
  color: var(--text-secondary);
}

/* Scroll Indicator */
.scroll-indicator-new {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-weight: 500;
}

.mouse-icon {
  width: 30px;
  height: 50px;
  border: 2px solid var(--primary-color);
  border-radius: 20px;
  position: relative;
  margin-bottom: 0.5rem;
}

.mouse-wheel {
  width: 4px;
  height: 10px;
  background: var(--primary-color);
  border-radius: 2px;
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  animation: scroll-wheel 2s infinite;
}

@keyframes scroll-wheel {
  0% {
    opacity: 1;
    top: 8px;
  }
  100% {
    opacity: 0;
    top: 28px;
  }
}

/* Responsive */
@media (max-width: 1024px) {
  .hero-content-new {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .hero-left {
    text-align: center;
  }

  .hero-badge,
  .hero-actions,
  .hero-stats {
    justify-content: center;
  }

  .hero-desc {
    margin-left: auto;
    margin-right: auto;
  }

  .name-large {
    font-size: 3.5rem;
  }

  .role-line {
    font-size: 2rem;
    justify-content: center;
  }

  .floating-badge {
    display: none;
  }
}

@media (max-width: 768px) {
  .hero-new {
    padding: 120px 0 60px;
  }

  .hero-container-new {
    padding: 0 20px;
  }

  .name-large {
    font-size: 2.5rem;
  }

  .role-line {
    font-size: 1.5rem;
  }

  .greeting {
    font-size: 1.2rem;
  }

  .hero-desc {
    font-size: 1rem;
  }

  .hero-actions {
    flex-direction: column;
    width: 100%;
  }

  .btn-hero-primary,
  .btn-hero-secondary {
    width: 100%;
    justify-content: center;
  }

  .hero-stats {
    flex-direction: column;
    width: 100%;
  }

  .stat-box {
    text-align: center;
  }

  .profile-card {
    padding: 2rem;
  }
}
