.projects-compact {
  padding: 120px 0;
  background: var(--light-color);
  position: relative;
  overflow: hidden;
}

/* Header */
.projects-header-compact {
  text-align: center;
  margin-bottom: 4rem;
}

.header-badge-projects {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.5rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  color: var(--primary-color);
  font-weight: 700;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.badge-icon-projects {
  font-size: 1.2rem;
}

.projects-title-compact {
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.projects-subtitle-compact {
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 700px;
  margin: 0 auto;
}

/* Filter Tabs */
.filter-tabs-compact {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
  flex-wrap: wrap;
}

.filter-tab-compact {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.8rem 2rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  font-weight: 600;
  font-size: 0.95rem;
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.tab-dot {
  width: 8px;
  height: 8px;
  background: var(--secondary-color);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.filter-tab-compact::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gradient);
  transition: left 0.3s ease;
  z-index: 0;
}

.filter-tab-compact:hover::before,
.filter-tab-compact.active::before {
  left: 0;
}

.filter-tab-compact:hover,
.filter-tab-compact.active {
  color: white;
  border-color: transparent;
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.3);
}

.filter-tab-compact:hover .tab-dot,
.filter-tab-compact.active .tab-dot {
  background: white;
}

.filter-tab-compact span {
  position: relative;
  z-index: 1;
}

/* Compact Projects Grid */
.projects-grid-compact {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.5rem;
}

/* Compact Project Card */
.project-card-compact {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 20px;
  padding: 1.5rem;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
  opacity: 0;
  animation: fadeInUp 0.6s ease forwards;
  display: flex;
  flex-direction: column;
  min-height: 280px;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.project-card-compact:hover {
  transform: translateY(-8px);
  border-color: var(--category-color);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.25);
}

/* Card Accent */
.card-accent-compact {
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.project-card-compact:hover .card-accent-compact {
  opacity: 1;
}

/* Card Header */
.card-header-compact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.project-icon-compact {
  width: 40px;
  height: 40px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  color: var(--primary-color);
  transition: all 0.3s ease;
}

.project-card-compact:hover .project-icon-compact {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: rotate(360deg);
}

.project-category-compact {
  font-size: 0.7rem;
  font-weight: 700;
  letter-spacing: 0.5px;
  text-transform: uppercase;
}

/* Card Body */
.project-title-compact {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.8rem;
  line-height: 1.3;
}

.project-description-short {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1rem;
  font-size: 0.9rem;
  flex: 1;
}

/* Tech Preview */
.tech-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 1rem;
}

.tech-chip-compact {
  padding: 0.3rem 0.8rem;
  background: var(--light-color);
  border: 1px solid var(--secondary-color);
  border-radius: 15px;
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--primary-color);
}

.tech-chip-compact.more {
  background: var(--gradient);
  color: white;
  border-color: transparent;
}

/* Card Footer */
.card-footer-compact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 2px solid var(--light-color);
}

.know-more-btn-card {
  background: none;
  cursor: pointer;
  border: none;
  border-radius: 50px;
}

.know-more-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.2rem;
  background: var(--gradient);
  color: white;
  border: none;
  border-radius: 50px;
  font-weight: 600;
  font-size: 0.85rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.3);
}

.know-more-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.5);
}

.quick-links {
  display: flex;
  gap: 0.5rem;
}

.quick-link {
  width: 35px;
  height: 35px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.3s ease;
  font-size: 0.95rem;
}

.quick-link:hover {
  background: var(--primary-color);
  color: white;
  border-color: transparent;
  transform: scale(1.1);
}

/* Modal Overlay */
.project-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  padding: 2rem;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Modal */
.project-modal {
  background: white;
  border-radius: 30px;
  max-width: 700px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  animation: slideUp 0.4s ease;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-close {
  position: absolute;
  top: 1.5rem;
  right: 1.5rem;
  width: 40px;
  height: 40px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1.2rem;
  color: var(--text-primary);
  z-index: 10;
}

.modal-close:hover {
  background: #ff4444;
  color: white;
  border-color: transparent;
  transform: rotate(90deg);
}

/* Modal Header */
.modal-header {
  padding: 2.5rem;
  border-bottom: 2px solid var(--light-color);
  display: flex;
  align-items: flex-start;
  gap: 1.5rem;
}

.modal-icon {
  width: 60px;
  height: 60px;
  background: var(--gradient);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  flex-shrink: 0;
}

.modal-title-section {
  flex: 1;
}

.modal-category {
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-transform: uppercase;
  display: block;
  margin-bottom: 0.5rem;
}

.modal-title-section h2 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1.3;
}

.modal-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.2rem;
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 700;
  flex-shrink: 0;
}

.modal-status.completed {
  background: #d1fae5;
  color: #059669;
}

.modal-status.in-progress {
  background: #fef3c7;
  color: #d97706;
}

/* Modal Body */
.modal-body {
  padding: 2.5rem;
}

.modal-section {
  margin-bottom: 2rem;
}

.modal-section:last-child {
  margin-bottom: 0;
}

.modal-section h3 {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.modal-section p {
  color: var(--text-secondary);
  line-height: 1.8;
  font-size: 1rem;
}

.modal-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.6rem;
}

.modal-tech-chip {
  padding: 0.6rem 1.2rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary-color);
  transition: all 0.3s ease;
}

.modal-tech-chip:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: scale(1.05);
}

.features-list {
  list-style: none;
  padding: 0;
}

.features-list li {
  padding: 0.8rem 0;
  border-bottom: 1px solid var(--light-color);
  color: var(--text-secondary);
  line-height: 1.6;
  position: relative;
  padding-left: 2rem;
}

.features-list li:last-child {
  border-bottom: none;
}

.features-list li::before {
  content: "";
  position: absolute;
  left: 0;
  color: var(--primary-color);
  font-weight: 700;
  font-size: 1.2rem;
}

/* Modal Footer */
.modal-footer {
  padding: 2rem 2.5rem;
  border-top: 2px solid var(--light-color);
  display: flex;
  gap: 1rem;
}

.modal-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  text-decoration: none;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.modal-btn.github {
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  color: var(--text-primary);
}

.modal-btn.github:hover {
  background: #24292e;
  color: white;
  border-color: transparent;
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
}

.modal-btn.demo {
  background: var(--gradient);
  color: white;
  border: 2px solid transparent;
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.3);
}

.modal-btn.demo:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(87, 185, 255, 0.5);
}

/* CTA Section */
.projects-cta-compact {
  margin-top: 5rem;
}

.cta-content-compact {
  max-width: 700px;
  margin: 0 auto;
  padding: 3rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 30px;
  text-align: center;
}

.cta-content-compact h3 {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.cta-content-compact p {
  color: var(--text-secondary);
  margin-bottom: 2rem;
  font-size: 1.05rem;
}

.cta-button-compact {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  padding: 1.2rem 2.5rem;
  background: var(--gradient);
  color: white;
  text-decoration: none;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1rem;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.3);
}

.cta-button-compact:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.5);
}

/* Responsive */
@media (max-width: 768px) {
  .projects-compact {
    padding: 80px 0;
  }

  .projects-title-compact {
    font-size: 2rem;
  }

  .projects-subtitle-compact {
    font-size: 1rem;
  }

  .projects-grid-compact {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
  }

  .project-modal {
    max-width: 100%;
    border-radius: 20px;
  }

  .modal-header {
    padding: 2rem;
    flex-direction: column;
  }

  .modal-title-section h2 {
    font-size: 1.5rem;
  }

  .modal-body {
    padding: 1.5rem;
  }

  .modal-footer {
    padding: 1.5rem;
    flex-direction: column;
  }

  .cta-content-compact {
    padding: 2rem;
  }

  .cta-content-compact h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .projects-grid-compact {
    grid-template-columns: 1fr;
  }

  .modal-close {
    top: 1rem;
    right: 1rem;
  }
}
