* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #57b9ff;
  --secondary-color: #90d5ff;
  --accent-color: #77b1d4;
  --dark-color: #517891;
  --light-color: #f0f8ff;
  --text-primary: #2c3e50;
  --text-secondary: #517891;
  --text-light: #7b8fa3;
  --gradient: linear-gradient(135deg, #90d5ff 0%, #57b9ff 50%, #517891 100%);
  --shadow-small: 0 2px 4px rgba(81, 120, 145, 0.1);
  --shadow-medium: 0 4px 6px rgba(81, 120, 145, 0.15);
  --shadow-large: 0 10px 20px rgba(81, 120, 145, 0.2);
}

body {
  font-family: "Plus Jakarta Sans" !important, -apple-system, BlinkMacSystemFont,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.6;
  color: var(--text-primary);
  overflow-x: hidden;
  background: var(--light-color);
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.highlight {
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.section-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 3rem;
  font-weight: 700;
  color: var(--text-primary);
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 768px) {
  .container {
    padding: 0 16px;
  }

  .section-title {
    font-size: 2rem;
  }
}
