/* Toggle Button - Fixed Left Side */
.sidebar-toggle {
  position: fixed;
  left: 20px;
  top: 20px;
  width: 60px;
  height: 60px;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 1001;
  transition: all 0.3s ease;
  box-shadow: 0 4px 20px rgba(87, 185, 255, 0.2);
}

.sidebar-toggle:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 30px rgba(87, 185, 255, 0.3);
  border-color: var(--primary-color);
}

.toggle-icon {
  position: relative;
  z-index: 2;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}

.sidebar-toggle.active .toggle-icon {
  color: var(--primary-color);
}

.toggle-bg {
  position: absolute;
  inset: 0;
  background: var(--gradient);
  border-radius: 14px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.sidebar-toggle.active .toggle-bg {
  opacity: 0.1;
}

/* Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  z-index: 999;
}

.sidebar-overlay.active {
  opacity: 1;
  visibility: visible;
}

/* Sidebar Navigation */
.sidebar-nav {
  position: fixed;
  left: -320px;
  top: 0;
  width: 320px;
  height: 100vh;
  background: white;
  box-shadow: 4px 0 30px rgba(87, 185, 255, 0.15);
  z-index: 1000;
  transition: left 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  display: flex;
  flex-direction: column;
  border-right: 2px solid var(--secondary-color);
}

.sidebar-nav.open {
  left: 0;
}

/* Sidebar Header */
.sidebar-header {
  padding: 2rem 2rem 1.5rem;
  border-bottom: 2px solid var(--light-color);
  text-align: center;
}

.sidebar-logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.3rem;
  margin-bottom: 1rem;
}

.logo-bracket-side {
  color: var(--primary-color);
  font-size: 2rem;
  font-weight: 700;
  animation: bounce-bracket 2s ease-in-out infinite;
}

.logo-icon-side {
  font-size: 2rem;
  color: var(--dark-color);
}

.sidebar-name {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 0.3rem;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.sidebar-title {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 500;
}

/* Sidebar Menu */
.sidebar-menu {
  flex: 1;
  padding: 2rem 0;
  overflow-y: auto;
}

.sidebar-link {
  position: relative;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 2rem;
  text-decoration: none;
  color: var(--text-primary);
  transition: all 0.3s ease;
  opacity: 0;
  transform: translateX(-20px);
}

.sidebar-nav.open .sidebar-link {
  opacity: 1;
  transform: translateX(0);
}

.link-number {
  font-size: 0.75rem;
  font-weight: 700;
  color: var(--primary-color);
  width: 30px;
  text-align: center;
  opacity: 0.6;
  transition: opacity 0.3s ease;
}

.link-icon {
  width: 40px;
  height: 40px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  font-size: 1.2rem;
  transition: all 0.3s ease;
}

.link-text {
  font-size: 1rem;
  font-weight: 600;
  flex: 1;
}

.link-indicator {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 0;
  background: var(--gradient);
  border-radius: 4px 0 0 4px;
  transition: height 0.3s ease;
}

.sidebar-link:hover {
  background: var(--light-color);
}

.sidebar-link:hover .link-number {
  opacity: 1;
}

.sidebar-link:hover .link-icon {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: scale(1.1) rotate(5deg);
}

.sidebar-link.active {
  background: var(--light-color);
}

.sidebar-link.active .link-number {
  opacity: 1;
}

.sidebar-link.active .link-icon {
  background: var(--gradient);
  color: white;
  border-color: transparent;
}

.sidebar-link.active .link-indicator {
  height: 50%;
}

/* Sidebar Footer */
.sidebar-footer {
  padding: 1.5rem 2rem 2rem;
  border-top: 2px solid var(--light-color);
}

.sidebar-cta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  background: var(--gradient);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 700;
  font-size: 0.95rem;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(87, 185, 255, 0.3);
  margin-bottom: 1.5rem;
}

.sidebar-cta:hover {
  transform: translateY(-3px);
  box-shadow: 0 8px 25px rgba(87, 185, 255, 0.5);
}

.cta-arrow {
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.sidebar-cta:hover .cta-arrow {
  transform: translateX(5px);
}

.sidebar-social {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.social-icon-sidebar {
  width: 40px;
  height: 40px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-color);
  transition: all 0.3s ease;
  text-decoration: none;
}

.social-icon-sidebar:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: translateY(-5px) rotate(5deg);
  box-shadow: 0 8px 20px rgba(87, 185, 255, 0.3);
}

/* Scrollbar for Sidebar Menu */
.sidebar-menu::-webkit-scrollbar {
  width: 4px;
}

.sidebar-menu::-webkit-scrollbar-track {
  background: var(--light-color);
}

.sidebar-menu::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 4px;
}

/* Responsive */
@media (max-width: 768px) {
  .sidebar-toggle {
    left: 15px;
    top: 15px;
    width: 55px;
    height: 55px;
  }

  .sidebar-nav {
    width: 280px;
    left: -280px;
  }

  .sidebar-header {
    padding: 1.5rem 1.5rem 1rem;
  }

  .sidebar-name {
    font-size: 1.3rem;
  }

  .sidebar-menu {
    padding: 1.5rem 0;
  }

  .sidebar-link {
    padding: 0.9rem 1.5rem;
  }

  .sidebar-footer {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .sidebar-nav {
    width: 260px;
    left: -260px;
  }
}

/* Animation */
@keyframes bounce-bracket {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
}
