.footer-alt {
  background: linear-gradient(180deg, #ffffff 0%, #f0f8ff 100%);
  position: relative;
  overflow: hidden;
}

/* Top CTA Section */
.footer-top {
  padding: 80px 0;
  background: var(--gradient);
  color: white;
  position: relative;
  overflow: hidden;
}

.footer-top::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
      circle at 20% 50%,
      rgba(255, 255, 255, 0.1),
      transparent 50%
    ),
    radial-gradient(
      circle at 80% 80%,
      rgba(255, 255, 255, 0.1),
      transparent 50%
    );
  pointer-events: none;
}

.footer-cta-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  flex-wrap: wrap;
  position: relative;
  z-index: 1;
}

.cta-content-alt h2 {
  font-size: 3rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.cta-content-alt p {
  font-size: 1.2rem;
  opacity: 0.95;
}

.cta-button-alt {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  padding: 1.2rem 2.5rem;
  background: white;
  color: var(--primary-color);
  text-decoration: none;
  border-radius: 50px;
  font-weight: 700;
  font-size: 1.1rem;
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.cta-button-alt::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: var(--light-color);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.cta-button-alt:hover::before {
  width: 400px;
  height: 400px;
}

.cta-button-alt span,
.cta-icon {
  position: relative;
  z-index: 1;
}

.cta-button-alt:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.cta-icon {
  font-size: 1.3rem;
  animation: pulse-icon 2s ease-in-out infinite;
}

@keyframes pulse-icon {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

/* Main Footer Content */
.footer-main {
  padding: 80px 0 40px;
}

.footer-grid-alt {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1.5fr;
  gap: 3rem;
}

/* Footer Columns */
.footer-col h4 {
  font-size: 1.2rem;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-weight: 700;
  position: relative;
  display: inline-block;
}

.footer-col h4::after {
  content: "";
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 40px;
  height: 3px;
  background: var(--gradient);
  border-radius: 2px;
}

/* About Column */
.about-col {
  max-width: 350px;
}

.footer-logo-alt {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  margin-bottom: 1rem;
}

.logo-bracket-footer {
  color: var(--primary-color);
  font-size: 2rem;
  font-weight: 700;
}

.logo-icon-footer {
  font-size: 2rem;
  color: var(--dark-color);
}

.about-col h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-weight: 700;
}

.footer-about-text {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1.5rem;
  font-size: 0.95rem;
}

.footer-social-alt {
  display: flex;
  gap: 1rem;
}

.social-link-alt {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 12px;
  color: var(--primary-color);
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.social-link-alt:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: translateY(-5px) rotate(5deg);
  box-shadow: 0 10px 25px rgba(87, 185, 255, 0.3);
}

/* Footer Lists */
.footer-list {
  list-style: none;
}

.footer-list li {
  margin-bottom: 0.8rem;
}

.footer-list a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.95rem;
}

.list-dot {
  width: 6px;
  height: 6px;
  background: var(--primary-color);
  border-radius: 50%;
  transition: all 0.3s ease;
}

.footer-list a:hover {
  color: var(--primary-color);
  padding-left: 0.5rem;
}

.footer-list a:hover .list-dot {
  background: var(--dark-color);
  transform: scale(1.5);
}

/* Contact List */
.footer-contact-list {
  list-style: none;
}

.footer-contact-list li {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
  margin-bottom: 1rem;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.contact-icon-alt {
  color: var(--primary-color);
  font-size: 1.2rem;
  flex-shrink: 0;
  margin-top: 2px;
}

.footer-contact-list a {
  color: var(--text-secondary);
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-contact-list a:hover {
  color: var(--primary-color);
}

/* Bottom Bar */
.footer-bottom-alt {
  border-top: 2px solid var(--secondary-color);
  padding: 2rem 0;
  margin-top: 2rem;
}

.footer-bottom-content-alt {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.copyright-alt {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.name-highlight-footer {
  color: var(--primary-color);
  font-weight: 700;
}

.footer-links-alt {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.footer-links-alt a {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.footer-links-alt a:hover {
  color: var(--primary-color);
}

.dot-separator {
  color: var(--text-secondary);
  opacity: 0.5;
}

/* Scroll to Top with Progress Ring */
.scroll-top-alt {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 60px;
  height: 60px;
  background: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 1.5rem;
  color: var(--primary-color);
  box-shadow: 0 4px 20px rgba(87, 185, 255, 0.3);
  transition: all 0.3s ease;
  z-index: 999;
}

.scroll-top-alt svg {
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(-90deg);
}

.progress-ring-circle {
  stroke-dasharray: 170;
  stroke-dashoffset: 170;
  transition: stroke-dashoffset 0.3s;
}

.scroll-top-alt:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(87, 185, 255, 0.5);
}

.scroll-top-alt:hover .progress-ring-circle {
  stroke-dashoffset: 0;
}

/* Responsive */
@media (max-width: 1024px) {
  .footer-grid-alt {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }

  .about-col {
    grid-column: 1 / -1;
    max-width: 100%;
  }
}

@media (max-width: 768px) {
  .footer-cta-section {
    flex-direction: column;
    text-align: center;
  }

  .cta-content-alt h2 {
    font-size: 2rem;
  }

  .cta-button-alt {
    width: 100%;
    max-width: 300px;
    justify-content: center;
  }

  .footer-main {
    padding: 60px 0 40px;
  }

  .footer-grid-alt {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }

  .footer-bottom-content-alt {
    flex-direction: column;
    text-align: center;
  }

  .scroll-top-alt {
    bottom: 20px;
    right: 20px;
    width: 55px;
    height: 55px;
  }
}
