.skills-bento {
  padding: 120px 0;
  background: white;
  position: relative;
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s ease;
}

.skills-bento.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Header */
.skills-header-bento {
  text-align: center;
  margin-bottom: 4rem;
}

.header-tag {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1.5rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  color: var(--primary-color);
  font-weight: 700;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.tag-icon {
  animation: rotate 3s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.skills-title-bento {
  font-size: 3rem;
  font-weight: 800;
  color: var(--text-primary);
  margin-bottom: 1rem;
}

.skills-subtitle-bento {
  font-size: 1.1rem;
  color: var(--text-secondary);
  max-width: 650px;
  margin: 0 auto;
}

/* Bento Grid - FIXED */
.bento-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  grid-auto-rows: minmax(200px, auto);
}

.bento-card {
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 24px;
  padding: 2rem;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.bento-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(87, 185, 255, 0.2);
  border-color: var(--primary-color);
}

/* Featured Card - Primary Stack */
.bento-card.featured {
  grid-column: span 2;
  grid-row: span 2;
  background: var(--gradient);
  color: white;
  border: none;
}

.card-header-bento {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.card-header-bento h3 {
  font-size: 1.8rem;
  font-weight: 700;
}

.card-count {
  padding: 0.4rem 1rem;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
}

.featured-skills {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex: 1;
}

.featured-skill-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  transition: all 0.3s ease;
}

.featured-skill-item:hover {
  background: rgba(255, 255, 255, 0.25);
  transform: translateX(10px);
}

.featured-icon {
  width: 45px;
  height: 45px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  flex-shrink: 0;
}

.featured-name {
  font-size: 1.1rem;
  font-weight: 600;
}

.card-bg-pattern {
  position: absolute;
  bottom: -50px;
  right: -50px;
  width: 200px;
  height: 200px;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.1), transparent);
  border-radius: 50%;
}

/* Category Cards - FIXED POSITIONING */
.bento-card.category-1 {
  grid-column: span 2;
  grid-row: span 1;
  background: var(--light-color);
}

.bento-card.category-2 {
  grid-column: span 2;
  grid-row: span 1;
}

.bento-card.category-3 {
  grid-column: span 2;
  grid-row: span 1;
}

.card-icon-large {
  font-size: 3rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.category-title-bento {
  font-size: 1.3rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.skill-list-mini {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  flex: 1;
  align-content: flex-start;
}

.skill-chip-mini {
  display: flex;
  align-content: center;

  gap: 0.4rem;
  padding: 0.5rem 1rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 50px;
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-primary);
  transition: all 0.3s ease;
  height: fit-content;
}

.skill-chip-mini:hover {
  background: var(--gradient);
  color: white;
  border: none;
  border-color: transparent;
  transform: scale(1.05);
}

.chip-icon {
  font-size: 1rem;
}

/* Tools Card - FIXED */
.bento-card.tools-card {
  grid-column: span 2;
  grid-row: span 1;
  background: linear-gradient(135deg, #f0f8ff, white);
}

.tools-header h4 {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.tools-grid-bento {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 0.8rem;
}

.tool-item-bento {
  padding: 1rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 12px;
  text-align: center;
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-primary);
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.tool-item-bento:hover {
  background: var(--primary-color);
  color: white;
  border-color: transparent;
  transform: scale(1.05);
}

/* Stats Card - FIXED */
.bento-card.stats-card {
  grid-column: span 4;
  /* grid-row: span 1; */
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: var(--light-color);
}

.stat-item-bento {
  text-align: center;
}

.stat-value {
  font-size: 2.5rem;
  font-weight: 800;
  background: var(--gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  line-height: 1;
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
  font-weight: 600;
  margin-top: 0.5rem;
}

/* Responsive */
@media (max-width: 1200px) {
  .bento-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .bento-card.featured {
    grid-column: span 2;
    grid-row: span 2;
  }

  .bento-card.category-1,
  .bento-card.category-2,
  .bento-card.category-3 {
    grid-column: span 2;
    grid-row: span 1;
  }

  .bento-card.tools-card {
    grid-column: span 2;
    grid-row: span 1;
  }

  .bento-card.stats-card {
    grid-column: span 2;
    grid-row: span 1;
  }

  .tools-grid-bento {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .skills-bento {
    padding: 80px 0;
  }

  .skills-title-bento {
    font-size: 2rem;
  }

  .skills-subtitle-bento {
    font-size: 1rem;
  }

  .bento-grid {
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
  }

  .bento-card.featured,
  .bento-card.category-1,
  .bento-card.category-2,
  .bento-card.category-3,
  .bento-card.tools-card,
  .bento-card.stats-card {
    grid-column: span 1;
    grid-row: auto;
  }

  .tools-grid-bento {
    grid-template-columns: repeat(2, 1fr);
  }

  .card-header-bento {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .bento-card {
    padding: 1.5rem;
  }

  .tools-grid-bento {
    grid-template-columns: 1fr;
  }

  .stat-value {
    font-size: 2rem;
  }

  .featured-skill-item {
    padding: 0.8rem;
  }

  .featured-icon {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }

  .featured-name {
    font-size: 1rem;
  }
}
/* Core Concepts Section */
.core-concepts-section {
  margin-top: 4rem;
  padding: 3rem;
  background: white;
  border: 2px solid var(--secondary-color);
  border-radius: 30px;
}

.concepts-title {
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--text-primary);
  text-align: center;
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.concepts-icon {
  font-size: 2rem;
}

.concepts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
}

.concept-card {
  padding: 2rem;
  background: var(--light-color);
  border: 2px solid var(--secondary-color);
  border-radius: 20px;
  transition: all 0.3s ease;
  text-align: center;
}

.concept-card:hover {
  background: var(--gradient);
  color: white;
  border-color: transparent;
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(87, 185, 255, 0.3);
}

.concept-card h4 {
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.concept-card p {
  font-size: 0.9rem;
  opacity: 0.9;
}

.concept-card:hover h4,
.concept-card:hover p {
  color: white;
}

@media (max-width: 768px) {
  .core-concepts-section {
    padding: 2rem;
    margin-top: 3rem;
  }

  .concepts-grid {
    grid-template-columns: 1fr;
  }
}
